
































































import util from "../../common/util.js";

export default {
	data(){
		return {
			goods:[],
			good:[],
			box:[],
			goodsindex:0,
			boxindex:0,
			showgoods:true,
			showbox:false,
			auto:true,
			code:""
		}
	},
	onLoad:function(){
		this.goods=util.getData();
	},
	onUnload:function(){
		util.savedata(this.goods);
	},
	onHide:function(){
		util.savedata(this.goods);
	},
	methods:{
		doScan:function(){
			uni.scanCode({
				success:function(res){
					this.code=res.result;
					let haveresult=false;
					if(this.showgoods){
						for (let i = 0; i < this.goods.length; i++) {
							if(this.goods[i].asin.trim() == this.code.trim()){
								this.good=this.goods[i];
								this.goodsindex=i;
								//this.goods[i].qty_check+=1;
								haveresult=true;
								break;
							}
						}
					}else{
						for (let i = 0; i < this.goods.length; i++) {
							let boxobj=this.goods[i].boxs;
							for (let j = 0; j < boxobj.length; j++) {
								if(boxobj[j].asin.trim() == this.code.trim()){
									this.goodsindex=i;
									this.boxindex=j;
									this.box=boxobj[j];
									//this.goods[i].box_count_check+=1;
									//this.goods[i].boxs[j].checked=true;
									haveresult=true;
									break;
								}
							}
							if(haveresult){
								break;
							}
						}
					}
					if(!haveresult){
						this.good=[];
						this.box=[];
						uni.showToast({
							icon:"none",
							title:"没有找到数据"
						});
					}else{
						if(this.auto){
							this.doTotal();
						}
					}
				}.bind(this)
			})
		},
		changeScan:function(e){
			if(e.detail.value){
				this.showbox=true;
				this.showgoods=false;
			}else{
				this.showbox=false;
				this.showgoods=true;
			}
		},
		doTotal:function(e){
			if(this.showbox){
				this.goods[this.goodsindex].box_count_check+=1;
				this.goods[this.goodsindex].boxs[this.boxindex].checked=true;
			}else{
				this.goods[this.goodsindex].qty_check+=1;
			}
			uni.showToast({
				icon:"none",
				title:"检查通过成功"
			});
		},
		autototal:function(e){
			if(e.detail.value){
				this.auto=true;
			}else{
				this.auto=false;
			}
		}
	}
}



